@import '../../variables'

.calc
  width: 539px
  height: 448px
  display: grid
  grid-template: 1fr 372px / 1fr 1fr
  column-gap: 56px
  row-gap: 56px

.calc__elements
  display: grid
  grid-template: auto/ 1fr
  row-gap: 12px

.display, .operations, .numbers, .results
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.06), 0px 4px 6px rgba(0, 0, 0, 0.1)
  padding: 4px
  border-radius: 6px
  cursor: grab
.dragging
  opacity: 0.5
.hidden
  display: none
